import React, { Component } from "react"
import { Router, Route } from "react-router-dom"
import { Menu } from "semantic-ui-react"

import GithubIcon from "./github.svg"
import LinkedInIcon from "./linkedin.svg"
import InstagramIcon from "./instagram.svg"
import Face from "./moi.jpg"

class App extends Component {
	render() {
		return (
			<div className="ui container" style={{ height: "90vh" }}>
				<Menu secondary pointing fluid widths={4} size={"large"}>
					<Menu.Item link>Work Experience</Menu.Item>
					<Menu.Item link>Projects</Menu.Item>
					<Menu.Item link>Skills</Menu.Item>
					<Menu.Item link>Education</Menu.Item>
				</Menu>
				<div className="ui middle aligned grid" style={{ height: "100%" }}>
					<div className="six wide column">
						<div className="ui center aligned basic segment">
							<img className="ui centered small circular image" src={Face} />
							<h1>Ujjval Kumaria</h1>
							<div className="ui divider" />
							<p>Corvallis, OR üåßÔ∏è</p>
							<p>
								I am a full-stack developer, which means if you give me one more
								task my stack will overflow.
							</p>

							<div className="item"></div>
							<div className="ui horizontal list" style={{ padding: "10px" }}>
								<div className="item">
									<a href="https://github.com/flashkicker" target="_blank">
										<img
											style={{ height: "32px", width: "32px" }}
											src={GithubIcon}
										/>
									</a>
								</div>
								<div className="item">
									<a
										href="https://www.linkedin.com/in/ujjval-kumaria-309a55124/"
										target="_blank"
									>
										<img
											style={{ height: "32px", width: "32px" }}
											src={LinkedInIcon}
										/>
									</a>
								</div>
								<div className="item">
									<a
										href="https://www.instagram.com/flashkicker/"
										target="_blank"
									>
										<img
											style={{ height: "32px", width: "32px" }}
											src={InstagramIcon}
										/>
									</a>
								</div>
							</div>
						</div>
					</div>
					<div className="ten wide column">
						<div className="ui basic segment"></div>
					</div>
				</div>
			</div>
		)
	}
}

export default App
